import React,{ useState,useEffect } from 'react'
import './App.css';

const Recipe = (props) =>{


  return(
    <div className="Recipe" onClick={props.onClick}>
        <h1>{props.title}</h1>
        <img src={props.image} alt="nothing"/>
    </div>
  );
}



const ExtendedRecipe = (props) =>{
  return(
    <div className="Extended Recipe">
        <h1>this is an extended Recipe</h1>
    </div>

  );


}




const App = () => {
  const [Recipes,SetRecipies] = useState([]);
  const [Search,setSearch] = useState('');
  const [Query,setQuery] = useState('');
  const [SelectedRecipe,SetSelectedRecipe] = useState(false);
  const APP_ID = "10860fdf";
  const APP_KEY = "daa9cae076e037855097584d65ab1055"; 


  useEffect(()=>{
    getRecipies();
  },[Query]);

  const getRecipies = async ()=>{
    const response = await fetch(`https://api.edamam.com/search?q=${Query}&app_id=${APP_ID}&app_key=${APP_KEY}&from=0&to=20`);
    const data = await response.json();
    console.log(data);
    SetRecipies(data.hits);
  }
  const UpdateSearch = e =>{
    setSearch(e.target.value);
  }

  const SearchRecipe = e =>{
      e.preventDefault();
      setQuery(Search);
      setSearch('');
      SetSelectedRecipe(false);
  }

  const SelectRecipe = (e) =>{
    const ClickedRecipe = e.target;
    console.log(ClickedRecipe);


    SetSelectedRecipe(true);
  }

  return (
    <div className="App">
        <form className="SearchForm" onSubmit={SearchRecipe}>
          <input className="SearchInput" type="text" placeholder="Search" value={Search} onChange={UpdateSearch}/>
          <button className="SearchButton" type="submit">Search</button>
        </form>
      
    {
    SelectedRecipe ?           
    <ExtendedRecipe />
    : 
    <div className="RecipesContainer">
    {Recipes.map(recipe =>(
      <Recipe title={recipe.recipe.label} image={recipe.recipe.image} onClick={SelectRecipe}/>
      ))}
    </div>  
    }
    
    </div>
  )}

export default App;
